<template>
  <div>
    <el-table
      stripe
      height="750"
      class="order-list"
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="orderNum"
        label="工单编号"
        width="">
        <template slot-scope="scope">{{ scope.row.orderNum }}</template>
      </el-table-column>
      <el-table-column
        prop="orderName"
        label="工单名称"
        width="">
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="客户名称"
        width="">
      </el-table-column>
      <el-table-column
        prop="stationName"
        label="电站名称"
        width=""
