      </el-table-column>
      <el-table-column
        prop="planTourTime"
        label="计划巡视时间"
        width=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="orderStatus"
        label="工单状态"
        width=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="tourType"
        label="巡视类型"
        width=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="tourCycle"
        label="巡视周期"
        width=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="tourReason"
        label="特巡原因"
        width=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="lastTourTime"
