  export default {
    data () {
      return {
        tableData3: [{
          orderNum: 'JX-001—XX',
          orderName: 'XX故障',
          customerName: 'B客户',
          stationName:'小琴站',
          tourScope:'整站',
          planTourTime:'2018-07-08',
          orderStatus:'已接收',
          tourType:'特殊巡视',
          tourCycle:'15日',
          tourReason:'-',
          lastTourTime:'2018-07-08',
        },{
          orderNum: 'JX-001—XX',
          orderName: 'XX故障',
          customerName: 'B客户',
          stationName:'小琴站',
          tourScope:'整站',
          planTourTime:'2018-07-08',
          orderStatus:'已接收',
          tourType:'特殊巡视',
          tourCycle:'15日',
          tourReason:'-',
          lastTourTime:'2018-07-08',
        },{
          orderNum: 'JX-001—XX',
          orderName: 'XX故障',
          customerName: 'B客户',
          stationName:'小琴站',
          tourScope:'整站',
          planTourTime:'2018-07-08',
          orderStatus:'已接收',
          tourType:'特殊巡视',
          tourCycle:'15日',
          tourReason:'-',
          lastTourTime:'2018-07-08',
        },{
          orderNum: 'JX-001—XX',
          orderName: 'XX故障',
          customerName: 'B客户',
          stationName:'小琴站',
          tourScope:'整站',
          planTourTime:'2018-07-08',
          orderStatus:'已接收',
          tourType:'特殊巡视',
          tourCycle:'15日',
          tourReason:'-',
          lastTourTime:'2018-07-08',
        }],
        multipleSelection: []
      }
    },
    mounted(){
      let that = this;
      that.getTourOrderList();
    },
    methods:{
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleView(index, row) {
        console.log(index, row);
      },
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleColse(index, row) {
        console.log(index, row);
      },
      /*获取计划检修列表*/
      getTourOrderList(){
        let that = this;
        let params = that.formData;
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getTourOrderList", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data.data;
              that.tableData3 = data;
            }
          });
      },
    },
    props:{
      formData:Object,
    }
  }
</script>

