<template>
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="巡视工单" name="tourOrder"></el-tab-pane>
        <el-tab-pane label="消缺工单" name="removeOrder"></el-tab-pane>
        <el-tab-pane label="检修工单" name="serviceOrder"></el-tab-pane>
      </el-tabs>
      <div>
        <tourOrder v-if="showTourOrder"></tourOrder>
        <removeOrder v-if="showRemoveOrder"></removeOrder>
        <serviceOrder v-if="showServiceOrder"></serviceOrder>
      </div>
    </div>
</template>

<script>
  import tourOrder from "./tourOrder.vue";
  import removeOrder from "./removeOrder.vue";
  import serviceOrder from "./serviceOrder.vue";
    export default {
        data () {
            return {
              activeName: 'tourOrder',
              showTourOrder:true,
              showRemoveOrder:false,
              showServiceOrder:false,


            }
        },
      mounted(){

      },
      methods: {
        handleClick(tab, event) {
          var that = this;
          if(tab.paneName == 'tourOrder'){
            that.showTourOrder = true;
            that.showRemoveOrder = false;
            that.showServiceOrder = false;
          }else if(tab.paneName == 'removeOrder'){
            that.showTourOrder = false;
            that.showRemoveOrder = true;
            that.showServiceOrder = false;
          }else if(tab.paneName == 'serviceOrder'){
            that.showTourOrder = false;
            that.showRemoveOrder = false;
            that.showServiceOrder = true;
          }
        },


      },
      components:{
        tourOrder,removeOrder,serviceOrder
      }

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .sub-tab .el-tabs--card>.el-tabs__header, .sub-tab .el-tabs--card>.el-tabs__header .el-tabs__nav, .sub-tab .el-tabs--card>.el-tabs__header .el-tabs__item.is-active, .sub-tab .el-tabs--card>.el-tabs__header .el-tabs__item{
    border: 0px!important;
    border-top: 0px!important;
    border-bottom: 0px!important;
    border-left: 0px!important;
    border-right: 0px!important;
  }
</style>



