      },
      handleColse(index, row) {
        console.log(index, row);
      },
      /*获取计划检修列表*/
      getServiceOrderList(){
        let that = this;
        let params = that.formData;
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServiceOrderList", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data.data;
              that.tableData3 = data;
            }
          });
      },
    }
  }
</script>

