<template>
  <div>
    <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
    <div class="left chart-wrap order-container">
      <div class="hidden">
        <div class="left sub-tab">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClick1">
            <el-tab-pane label="工单列表" name="orderList"></el-tab-pane>
            <el-tab-pane label="工单统计" name="orderSta"></el-tab-pane>
          </el-tabs>
        </div>
        <div class="right form-list">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="检修类型">
            <el-select v-model="form.value" placeholder="请选择" size="small">
            <el-option
            v-for="item in form.serviceType"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="工单状态">
              <el-select v-model="form.value1" placeholder="请选择" size="small">
                <el-option
                  v-for="item in form.orderStatus"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            </el-form-item>
            <el-form-item label="时间">
              <el-date-picker
                v-model="form.dayDate"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="" class="btn-confirm">
              <el-button size="mini" @click.native="searchAll">确认</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="order-list-wrap" v-if="showOrderList">
        <vOrderList ref="vOrderList"></vOrderList>
      </div>
      <div class="order-sta-wrap" v-if="showOrderSta">
        <vOrderSta ref="vOrderSta"></vOrderSta>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  import TreeOrg from "../../../common/TreeOrg.vue";
  import vOrderList from "./serviceOrderList.vue";
  import vOrderSta from "./serviceOrderSta.vue";
  export default {
    data () {
      return {
        activeName: 'orderList',
        showOrderList:true,
        showOrderSta:false,
        treeData:[
          {
            id:11,
            label: "斗门区 (12)",
            children: [
              {
                id:21,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:22,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:23,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              }
            ]
          },
          {
            id:12,
            label: "金湾区 (6)",
            children: [
              {
                label: "二级 2-1",
                children: [
                  {
                    label: "三级 2-1-1"
                  }
                ]
              },
              {
                label: "二级 2-2",
                children: [
                  {
                    label: "三级 2-2-1"
                  }
                ]
              }
            ]
          },
          {
            label: "横琴新区 (6)",
            children: [
              {
                label: "二级 3-1",
                children: [
                  {
                    label: "三级 3-1-1"
                  }
                ]
              },
              {
                label: "二级 3-2",
                children: [
                  {
                    label: "三级 3-2-1"
                  }
                ]
              }
            ]
          }
        ],
        form: {
          serviceType: [],
          orderStatus:[],
          value: 0,
          value1: 0,
          dayDate:[new Date(),new Date()]
        },
      }
    },
    mounted(){
      let that = this;
      that.getServiceType();
      that.getOrderStatus();
    },
    methods:{
      searchAll(){
        let that = this;
        if(that.activeName == 'orderList'){
          //通过$refs可以直接调用子组件orderList里面的方法
          that.$refs.vOrderList.getServiceOrderList();
        }else if(that.activeName == 'orderSta'){
          that.$refs.vOrderSta.getServicePlanStaItem();
          that.$refs.vOrderSta.getServicePlanSta();
          that.$refs.vOrderSta.getServicePlanAny();
          that.$refs.vOrderSta.getServicePlanDelSta();
          that.$refs.vOrderSta.getServiceTroubleAny();
        }
      },
      PVviewById:function(id){

      },
      handleClick1(tab, event) {
        var that = this;
        if(tab.paneName == 'orderSta'){
          that.showOrderSta = true;
          that.showOrderList = false;
        }else if(tab.paneName == 'orderList'){
          that.showOrderSta = false;
          that.showOrderList = true;
        }
      },
      /*获取检修类型*/
      getServiceType(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/common/getServiceType", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.form.serviceType = data;
            }
          });
      },
      /*获取工单状态列表*/
      getOrderStatus(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/common/getOrderStatus", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.form.orderStatus = data;
            }
          });
      },
    },
    components: {
      TreeOrg,vOrderList,vOrderSta
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



